.current {
    color: green;
}

.logo {
    max-width: 250px;
}

h1, h2, h3, h4, h5, a {
    font-family: 'obvia', sans-serif !important;
}

p {
    font-size: 1.25em;
    line-height: 1.5em;
    letter-spacing: 2px;
}

