.hero {
    position: relative;
}

.hero:before {
    content: ' ';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 150%;
  height: 300%;
  opacity: 0.1;
  background-image: url('../images/DJR_BLACK_LOGO.png');
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;
  z-index: -999;
}

.hero-body {
    position: relative;
}

ul {
    li {
        p {
            display: inline;
        }
    }
}